.header {
    height: 80px;
    max-width: 1280px;
    padding: 0 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header__links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    column-gap: 50px;
}

@media screen and (max-width: 1024px) {
    .header {
        padding: 0 24px;
    }

    .header__links {
        display: none;
    }

    h3 {
        min-width: unset;
    }
}

.header__links a {
    font-family: Gilroy;
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    letter-spacing: 0.01em;
    text-align: left;
    color: #000000;
    text-decoration: none;
}

.header__apps {
    padding-left: 58px;
    display: flex;
    column-gap: 8px;
}