.main {
    padding-top: 200px;
}

@media screen and (max-width: 1024px) {
    .main {
        padding-top: 26px;
    }

    .main h3 {
        min-width: unset!important;
    }

    .main__box {
        width: 100%!important;
    }
}

.main h3 {
    font-family: RF Tone;
    font-size: 40px;
    font-weight: 600;
    line-height: 48px;
    letter-spacing: 0.01em;
    margin-bottom: 20px;
    min-width: 460px;
}

.main p {
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0px;
    text-align: center;
    margin-bottom: 40px;
}

.main__select {
    border-radius: 100px;
    border: 1px solid #DADADA;
    display: flex;
    flex-direction: row;
    justify-content: stretch;
    width: 100%;
    margin-bottom: 30px;
    overflow: hidden;
}

.main__select_item {
    cursor: pointer;
    flex: 1 1 auto;
    padding: 10px;
    text-align: center;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.10000000149011612px;
    text-align: center;
}

.main__select_item.active {
    background: #2E00E4;
    color: white;
}

.main__select_item:first-child {
    border-right: 1px solid #DADADA;
}

.main__box {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    text-align: center;
    width: fit-content;
}

.main__code {
    width: 100%;
}

.main__code p{
    margin-top: 40px;
    text-align: left;
}

.main__btn {
    font-family: Raleway;
    font-size: 14px;
    font-weight: 700;
    line-height: 36px;
    letter-spacing: 0.1em;
    text-align: left;
    color: #2E00E4;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-transform: uppercase;
}

.main__btn img {
    margin-left: 4px;
}

.main__code input{
    background: #F9F9F9!important;
    width: 65px!important;
    height: 80px!important;
    padding: 18px!important;
    border-radius: 10px!important;
    border: none!important;
    box-shadow: none!important;
    text-align: center!important;
}

.main__code input::-webkit-outer-spin-button,
.main__code input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
.main__code input[type=number] {
    -moz-appearance: textfield;
}

.main__link {
    cursor: pointer;
    display: flex;
    width: 100%;
    justify-content: space-between;
    font-family: Gilroy;
    font-size: 16px;
    font-weight: 600;
    line-height: 34px;
    letter-spacing: 0.01em;
    text-align: left;
}

.main__time {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: SF UI Display;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.07038461416959763px;
    text-align: left;
}

.main__timer {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: SF UI Display;
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.07038461416959763px;
    text-align: left;
    color: #2E00E4;
}

.main__info p {
    font-family: Gilroy;
    font-size: 16px;
    font-weight: 500;
    line-height: 45px;
    letter-spacing: 0.01em;
    text-align: left;
}

.main__info img {
    max-width: 100%;
}

.main__timer img {
    margin: 0 5px 0 6px
}

.main__buttons {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
}

.main__buttons button:first-child {
    border: 1px solid #2E00E4!important;
    background: white!important;
    color: #2E00E4!important;
}